import Typography from 'typography';

const typography = new Typography({
	baseFontSize: '18rem',
	// baseFontSize: '18px',
	baseLineHeight: 1.23,
	headerFontFamily: [
		'fantasque-sans-mono',
		'Avenir Next',
		'Helvetica Neue',
		'Segoe UI',
		'Helvetica',
		'Arial',
		'sans-serif'
	],
	bodyFontFamily: [ 'Fantasque Sans Mono', 'sans-serif' ]
});

export default typography;



